@import '../../styles/mixins';

.footer {
	background: #111;
	color: $mainWhiteColor;
	border-top: 1px solid rgba($color: #fff, $alpha: .2);

	&__container {}

	&__content {
		display: flex;
		flex-direction: column;
		gap: 30px;
		align-items: center;
		@include adaptiv-value('padding-top', 100, 50, 0);
		@include adaptiv-value('padding-bottom', 30, 30, 0);
	}

	&__title {
		@include adaptiv-value('font-size', 32, 16, 0);
	}

	&__text {
		@include adaptiv-value('font-size', 25, 14, 0);
	}

	&__form {
		display: flex;
		gap: 15px;

		input {
			border: 1px solid #fff;
			@include adaptiv-value('padding-top', 6, 4, 0);
			@include adaptiv-value('padding-bottom', 6, 4, 0);
			@include adaptiv-value('padding-left', 15, 10, 0);
			@include adaptiv-value('padding-right', 15, 10, 0);
			@include adaptiv-value('font-size', 20, 16, 0);
			font-weight: bold;
			border-radius: 3px;
		}
	}

	&__form__btn {
		font-weight: bold;
		border-radius: 3px;
		transition: all 0.3s ease 0s;
		color: inherit;
		border: 1px solid #fff;
		@include adaptiv-value('padding-top', 6, 4, 0);
		@include adaptiv-value('padding-bottom', 6, 4, 0);
		@include adaptiv-value('padding-left', 15, 10, 0);
		@include adaptiv-value('padding-right', 15, 10, 0);
		@include adaptiv-value('font-size', 20, 16, 0);

		&:hover {
			background: #fff;
			color: #000;
		}
	}

}

.nav {
	display: grid;
	@include adaptiv-value('gap', 150, 30, 0);
	grid-template-columns: 1fr 1fr 1fr 1fr;

	&__items {
		display: flex;
		@include adaptiv-value('gap', 150, 30, 0);
	}

	&__item {
		display: flex;
		flex-direction: column;
		gap: 15px;
	}

	&__title {
		font-weight: bold;
		@include adaptiv-value('font-size', 26, 18, 0);
	}

	&__list {
		display: flex;
		flex-direction: column;
		gap: 6px;
	}

	&__link {
		cursor: pointer;
		@include adaptiv-value('font-size', 18, 14, 0);
		font-weight: medium;
		color: $mainSilverColor;

		&:hover {
			color: $mainWhiteColor;
		}
	}
}

.copyright {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 90%;
	@include adaptiv-value('padding-top', 50, 20, 0);

	&__logo {
		font-weight: bold;
		@include adaptiv-value('font-size', 52, 22, 0);
	}

	&__text {
		@include adaptiv-value('font-size', 18, 12, 0);
	}

	&__socials {
		display: flex;
		@include adaptiv-value('gap', 20, 5, 0);
	}

	&__social {
		cursor: pointer;

		svg {
			@include adaptiv-value('width', 30, 25, 0);
			@include adaptiv-value('height', 30, 25, 0);
			color: #fff;
			fill: #fff;
		}

		&:nth-child(2) {
			&:hover {
				svg {
					fill: #d6249f;
				}
			}
		}

		&:nth-child(3) {
			&:hover {
				svg {
					fill: #3b5998;
				}
			}
		}

		&:nth-child(4) {
			&:hover {
				svg {
					fill: #00acee;
				}
			}
		}

		&:nth-child(1) {
			&:hover {
				svg {
					fill: #00aff0;
				}
			}
		}
	}
}

@media(max-width: 600px) {
	.copyright {
		max-width: 100%;
	}
}

@media(max-width: 550px) {
	.nav {
		grid-template-columns: 1fr 1fr;

		&__items {
			width: 100%;
			justify-content: center;
		}
	}
}