@import '../../styles/mixins';

.card {
	&__item {
		cursor: pointer;
		width: 100%;
		border-radius: 10px;
		box-shadow: 0 6px 20px rgba(56, 125, 255, 0.37);

		&:hover {
			.card__item__img {
				img {
					transform: scale(1.1);
				}
			}

			.card__item__text {
				color: #fff;
			}
		}
	}

	&__item__img {
		position: relative;
		width: 100%;
		padding-top: 67%;
		overflow: hidden;
		border-top-left-radius: 10px;
		border-top-right-radius: 10px;

		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
			transition: all 0.2s linear;
		}

		&::before {
			content: attr(data-category);
			position: absolute;
			bottom: 0;
			left: 15px;
			padding: 6px 8px;
			@include adaptiv-value('font-size', 18, 14, 0);
			color: #fff;
			background-color: #1f98f4;
			z-index: 1;
		}
	}

	&__item__text {
		color: inherit;
		font-weight: 700;
		@include adaptiv-value('padding-left', 30, 15, 0);
		@include adaptiv-value('padding-right', 30, 15, 0);
		@include adaptiv-value('padding-top', 20, 10, 0);
		@include adaptiv-value('padding-bottom', 20, 12, 0);
		@include adaptiv-value('font-size', 18, 14, 0);
	}
}