@import 'styles/mixins';

.products {
	height: 100%;
	min-height: 100vh;
	background: #123;
	padding: 60px 0;

	&__content {
		display: flex;
		flex-direction: column;
		gap: 30px;
		padding: 50px 0 0 0;
	}

	&__list {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		@include adaptiv-value('gap', 50, 30, 0);
		color: lightgray;
	}

	&__loader {}

	&__title {
		@include adaptiv-value('font-size', 32, 18, 0);
		text-align: center;
		@include adaptiv-value('margin-top', 50, 30, 0);
	}


	&__amount__pages {
		display: flex;
		gap: 20px;
	}

	&__amount {
		cursor: pointer;
		@include adaptiv-value('font-size', 18, 14, 0);
		padding: 5px 10px;
		border: 1px solid #fff;
		position: relative;
		transition: all 0.3s ease 0s;

		&:hover {
			border-color: #387dff;
		}

		&.active {
			border-color: #387dff;
		}
	}
}

.nav {
	&__list {
		display: flex;
		@include adaptiv-value('gap', 50, 10, 0);
		justify-content: center;
		flex-wrap: wrap;
	}

	&__link {
		border: 1px solid lightgray;
		border-radius: 50px;
		color: lightgray;
		cursor: pointer;
		white-space: nowrap;
		@include adaptiv-value('font-size', 22, 14, 0);
		@include adaptiv-value('padding-left', 15, 8, 0);
		@include adaptiv-value('padding-right', 15, 8, 0);
		@include adaptiv-value('padding-top', 10, 5, 0);
		@include adaptiv-value('padding-bottom', 10, 5, 0);

		&.active {
			border: 1px solid #fff;
			color: #fff;
		}

		&:hover {
			border: 1px solid #fff;
			color: #fff;
		}
	}
}

@media(min-width: 2000px) {
	.products__list {
		grid-template-columns: 1fr 1fr 1fr 1fr;
	}
}

@media(max-width: 1000px) {
	.products__list {
		grid-template-columns: 1fr 1fr;
	}
}

@media(max-width: 600px) {
	.products__list {
		grid-template-columns: 1fr;
	}
}