@import 'styles/mixins';

.admin {

	&__body {
		background: #fff;
		color: $mainBlackColor;
		box-shadow: 0px 0px 20px #fff;
		display: flex;
		@include adaptiv-value('padding-left', 30, 10, 0);
		@include adaptiv-value('padding-right', 30, 10, 0);
		flex-direction: column;
		align-items: center;
		@include adaptiv-value('gap', 20, 10, 0);
		@include adaptiv-value('width', 600, 300, 0);
	}

	&__loader {
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		background: rgba($color: #000000, $alpha: .7);
		display: flex;
		justify-content: center;
		align-items: center;
		color: #fff;
		position: fixed;
		z-index: 100;
	}

	&__title {
		@include adaptiv-value('font-size', 30, 18, 0);
	}

	&__form {
		display: flex;
		flex-direction: column;
		@include adaptiv-value('gap', 20, 10, 0);
		padding: 10px 0;
		width: 100%;
	}

	&__file__label {
		background: #34b2d9;
		@include adaptiv-value('font-size', 20, 16, 0);
		color: #fff;
		padding: 5px;
		cursor: pointer;
	}

	&__file {
		display: none;
	}

	&__input {
		resize: none;
		border: 1px solid #111;
		@include adaptiv-value('padding-top', 10, 8, 0);
		@include adaptiv-value('padding-bottom', 10, 8, 0);
		@include adaptiv-value('padding-left', 15, 10, 0);
		@include adaptiv-value('padding-right', 15, 10, 0);
		@include adaptiv-value('font-size', 16, 14, 0);
	}

	&__list {
		position: relative;

		h1 {
			background: #34b2d9;
			@include adaptiv-value('font-size', 20, 16, 0);
			color: #fff;
			padding: 5px;
			cursor: pointer;
		}

		&:hover {
			z-index: 1;
		}
	}

	&__list__open {
		position: absolute;
		opacity: 0;
		width: 100%;
		background: #fff;
		color: inherit;
		height: fit-content;
		transform: scaleY(0);
		@include adaptiv-value('font-size', 18, 14, 0);

		&.active {
			opacity: 1;
			top: 100%;
			transform: scaleY(1);
		}
	}

	&__link {
		cursor: pointer;
		padding: 5px 0;
		border-bottom: 1px solid rgba($color: #000000, $alpha: .7);

		&:hover {
			color: #34b2d9;
			border-color: #34b2d9;
		}
	}

	&__btn {
		background: #34b2d9;
		border-radius: 20px;
		color: #fff;
		width: max-content;
		@include adaptiv-value('padding-top', 10, 8, 0);
		@include adaptiv-value('padding-bottom', 10, 8, 0);
		@include adaptiv-value('padding-left', 20, 12, 0);
		@include adaptiv-value('padding-right', 20, 12, 0);
		@include adaptiv-value('font-size', 16, 14, 0);
	}
}