@import 'styles/mixins';

.admin {
	background: #123;
	width: 100%;
	height: 100vh;

	&__enter__form {
		@include adaptiv-value('width', 500, 300, 0);
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: relative;
		z-index: 1;
		gap: 30px;
		font-size: 30px;
		background: #123;

		input {
			width: 100%;
			padding: 10px 20px;
			font-size: 18px;
		}
	}

	&__loader {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100vh;
	}

	&__password {
		display: flex;
		position: relative;
		width: 100%;
	}

	&__password__see {
		height: 100%;
		width: 20px;
		position: absolute;
		right: 20px;
		display: flex;
		justify-content: center;
		align-items: center;

		svg {
			color: #000;
		}
	}

	&__container {}

	&__content {
		width: 100%;
		height: 100vh;
		display: flex;
		position: absolute;
		top: 0;
		left: 0px;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	&__btns {
		display: flex;
		flex-direction: column;
		gap: 20px;
	}

	&__btn {
		@include adaptiv-value('font-size', 32, 18, 0);
		color: $mainWhiteColor;
	}

	&__popup {
		width: 100%;
		height: 100vh;
		background: rgba(0, 0, 0, .6);
		z-index: -10;
		position: fixed;
		display: flex;
		justify-content: center;
		align-items: center;
		opacity: 0;
		transform: scale(0);
		transition: all 0.3s ease 0s;

		&.active {
			opacity: 1;
			z-index: 10;
			transform: scale(1);
		}
	}
}