@import 'styles/mixins';

.admin {
	&__list {
		background: $mainWhiteColor;
		display: flex;
		flex-direction: column;
		gap: 30px;
		padding: 10px 30px;
	}

	&__title {
		color: $mainBlackColor;
		text-align: center;
		@include adaptiv-value('font-size', 30, 18, 0);
	}

	&__input {
		@include adaptiv-value('font-size', 18, 14, 0);
		@include adaptiv-value('padding-top', 10, 8, 0);
		@include adaptiv-value('padding-bottom', 10, 8, 0);
		border-bottom: 1px solid rgba($color: #000000, $alpha: .7);
	}

	&__btn {
		background: #34b2d9;
		color: $mainWhiteColor;
		width: max-content;
		padding: 10px 15px;
		border-radius: 20px;
	}

	&__loader {
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		background: rgba($color: #000000, $alpha: .7);
		display: flex;
		justify-content: center;
		align-items: center;
		color: #fff;
		position: fixed;
		z-index: 100;
	}
}