@import '../../../styles/mixins';

.main {
	width: 100%;
	min-height: 100vh;
	height: 100%;
	position: relative;
	overflow: hidden;
	display: flex;
	justify-content: center;
	align-items: center;

	video {
		top: 0;
		left: 0;
		object-fit: cover;
		width: 100%;
		height: 100%;
		position: absolute;
		z-index: -1;
		background: #111;
	}

	&__content {
		display: flex;
		flex-direction: column;
		gap: 30px;
		width: 100%;
		height: 100%;
		align-items: center;
	}

	&__title {
		@include adaptiv-value('font-size', 86, 26, 0);
		font-weight: 700;
	}

	&__text {
		@include adaptiv-value('font-size', 36, 20, 0);
	}

	&__btns {
		display: flex;
		gap: 0 15px;
	}
}