@import '../../styles/mixins';

.header {
	position: fixed;
	width: 100%;
	height: 60px;
	z-index: 1000;
	padding: 0 15px;
	transition: all 0.3s ease 0s;

	&__container {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 100%;
	}

	&__logo {
		font-family: $fontFamily;
		@include adaptiv-value('font-size', 56, 26, 0);
		font-weight: 700;
		cursor: pointer;
	}

	&__burger {
		display: none;
	}
}

.menu {
	height: 100%;

	&__list {
		display: flex;
		gap: 30px;
		font-weight: 700;
		height: 100%;
		overflow: hidden;
	}

	&__link {
		color: $mainSilverColor;
		@include adaptiv-value('font-size', 26, 18, 0);
		cursor: pointer;
		transition: all 0.3s ease 0s;
		position: relative;
		height: 100%;
		display: flex;
		align-items: center;

		&::before {
			position: absolute;
			content: '';
			bottom: -5px;
			left: 0;
			width: 100%;
			background: #fff;
			height: 3px;
		}

		&.active {
			color: inherit;

			&::before {
				bottom: 0;
			}

			&:hover {
				color: inherit;
				transform: translate(0, 0px);
			}
		}

		&:hover {
			color: inherit;
			transform: translate(0, -5px);
		}
	}
}

@media(max-width: 991px) {
	.header {
		&__burger {
			position: relative;
			width: 30px;
			height: 20px;
			display: flex;
			justify-content: center;
			align-items: center;

			span {
				position: absolute;
				width: 100%;
				height: 2px;
				background: #fff;
				top: 0;
				transition: all 0.3s ease 0s;

				&:nth-child(2) {
					top: 50%;
					transform: translate(0, -50%);
				}

				&:nth-child(3) {
					top: 100%;
					transform: translate(0, -100%);
				}
			}

			&.active {
				span {
					top: 50%;
					transform: translate(0, -50%) rotate(-45deg);

					&:nth-child(2) {
						opacity: 0;
					}

					&:nth-child(3) {
						top: 50%;
						transform: translate(0, -50%) rotate(45deg);
					}
				}
			}
		}
	}

	.menu {
		top: 0;
		left: 0;
		background: rgba(0, 0, 0, .8);
		position: fixed;
		height: 0;
		width: 100%;
		transition: all .3s ease 0s;

		&.active {
			height: 100vh;
			transition: all 0.6s ease 0s;
		}

		&__list {
			flex-direction: column;
			width: 100%;
			height: 100%;
			justify-content: center;
			align-items: center;
		}

		&__link {
			&::before {
				content: none;
			}
		}
	}
}