@import 'styles/mixins';

.loader {
	@include adaptiv-value('font-size', 35, 18, 0);
	text-align: center;

	span {
		width: 10px;
		height: 10px;
		background: #fff;
		border-radius: 50%;
		display: inline-block;
		transform: scale(1);
		animation: animLoading 1.5s ease-in-out infinite both;
		margin-left: 10px;

		&:nth-child(1) {
			animation-delay: -0.30s;
		}

		&:nth-child(2) {
			animation-delay: -0.15s;
		}
	}
}

@keyframes animLoading {

	0%,
	80%,
	100% {
		transform: scale(0);
	}

	40% {
		transform: scale(1);
	}
}