@import 'styles/mixins';

.product {
	height: 100%;
	min-height: 100vh;
	background-position: center;
	background-size: cover;
	position: relative;

	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, .25);
	}

	&__content {}

	&__title {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		color: #fff;
		font-size: 100px;
	}

	&__loader {
		width: 100%;
		height: 100vh;
		background: #123;
		display: flex;
		justify-content: center;
		align-items: center;
	}
}