@import '../../../styles/mixins';

.gallery {
	color: $mainBlackColor;
	height: 100%;
	min-height: 100vh;
	@include adaptiv-value('padding-top', 50, 20, 0);

	&__container {}

	&__title {
		@include adaptiv-value('font-size', 36, 18, 0);
		text-align: center;
	}

	&__content {
		width: 100%;
		gap: 30px;
		@include adaptiv-value('padding-top', 50, 20, 0);
		@include adaptiv-value('padding-bottom', 100, 50, 0);
		display: flex;
		flex-direction: column;
	}

	&__items {
		display: flex;
		gap: 30px;
	}
}

@media(max-width: 991px) {
	.gallery {
		&__items {
			flex-direction: column;
		}
	}
}