@import '../../../styles/mixins';

.service {
	background: #123;
	min-height: 100vh;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;

	&__container {
		width: 100%;
	}

	&__content {
		display: flex;
		width: 100%;
		justify-content: space-between;
		align-items: center;
		@include adaptiv-value('padding-left', 100, 50, 0);
		@include adaptiv-value('padding-right', 100, 50, 0);
		@include adaptiv-value('gap', 50, 30, 0);
	}

	&__item {
		*+* {
			margin-top: 30px;
		}

		&:nth-child(1) {
			@include adaptiv-value('margin-top', 50, 30, 0);
		}
	}

	&__title {
		@include adaptiv-value('font-size', 52, 26, 0);
	}

	&__text {
		@include adaptiv-value('font-size', 24, 18, 0);
		@include adaptiv-value('max-width', 500, 300, 0);
		background: linear-gradient(272.26deg, #E48772 -9.99%, #C71BCB 115.08%);
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		background-clip: text;
		text-fill-color: transparent;

	}

	&__btn {
		color: #fff;
		cursor: pointer;
		// background: linear-gradient(95.97deg, #C71BCB -35.61%, #E48772 121.12%);
		border: 2px solid transparent;
		border-image: linear-gradient(to left top, #E48772 10%, transparent 30%, transparent 70%, #C71BCB 90%);
		border-image-slice: 1;
		@include adaptiv-value('padding-top', 10, 6, 0);
		@include adaptiv-value('padding-bottom', 10, 6, 0);
		@include adaptiv-value('padding-left', 20, 16, 0);
		@include adaptiv-value('padding-right', 20, 16, 0);
		@include adaptiv-value('font-size', 18, 14, 0);
		position: relative;
		transition: color .3s ease 0s;
		z-index: 1;
		overflow: hidden;

		&::before {
			content: '';
			position: absolute;
			background: linear-gradient(to left top, #E48772, #C71BCB);
			left: 0px;
			top: 0px;
			width: 100%;
			height: 100%;
			z-index: -1;
			opacity: 0;
			transition: all 0.3s ease 0s;
			border: 2px solid transparent;
		}

		&:hover {
			border: 2px solid transparent;
			color: #000;

			&::before {
				opacity: 1;
			}
		}
	}


	&__img {
		@include adaptiv-value('max-width', 550, 300, 0);

		img {
			width: 100%;
		}
	}
}

@media(max-width: 700px) {
	.service {
		&__content {
			flex-direction: column-reverse;
			justify-content: none;
			gap: 0;
		}
	}
}