@import '../../../styles/mixins';

.btn {
	color: #fff;
	padding: 15px 30px;
	background: transparent;
	border: 1px solid #fff;
	@include adaptiv-value('padding-left', 30, 12, 0);
	@include adaptiv-value('padding-right', 30, 12, 0);
	@include adaptiv-value('padding-top', 15, 8, 0);
	@include adaptiv-value('padding-bottom', 15, 8, 0);
	@include adaptiv-value('font-size', 18, 14, 0);
	border-radius: 5px;
	font-weight: bold;
	transition: all 0.3s ease 0s;

	&:hover {
		background: #fff;
		color: #000;
	}

	&__primary {
		background: #fff;
		color: #000;

		&:hover {
			background: transparent;
			color: inherit;
		}
	}
}